.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 24px;

    @media screen and (min-width: 390px) {
        width: 390px;
    }

    @media screen and (min-width: 1334px) {
        width: 1334px;
    }

    @media screen and (min-width: 1766px) {
        width: 1766px;
    }
}